import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Helmet from "react-helmet";
import Loader from "../../components/Loader";
import Section from "../../components/Section";
import Message from "../../components/Message";
import Poster from "../../components/Poster";

const Container = styled.div`
  padding: 20px;
  margin: 0 10%;
`;
const FormDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;
const Form = styled.form`
  margin-bottom: 50px;
  position: relative;
  width: 80%;
`;

const Input = styled.input`
  all: unset;
  font-size: 28px;
  width: 100%;
  padding: 20px 30px;
  background-color: white;
  color: darkgray;
  border-radius: 30px;
  font-weight: 300;
`;

const SearchLogo = styled.span`
  width: 50px;
  height: 50px;
  position: absolute;
  top: 15%;
  right: 0%;
  background-position: center center;
  background-size: cover;
  &:active{
    opacity: 0.5;
  }
  background-image: url(data:image/svg+xml;base64,);
`;

interface ISearchPresenterProps {
  movieResults: any[] | null,
  tvResults: any[] | null,
  loading: boolean,
  searchTerm?: string,
  searched?: string,
  handleSubmit: (event: React.FormEvent) => void,
  error: string | null,
  updateTerm: (event: any) => void
}
const SearchPresenter: React.FunctionComponent<ISearchPresenterProps> = ({
  movieResults,
  tvResults,
  loading,
  searchTerm,
  searched,
  handleSubmit,
  error,
  updateTerm
}) => (
  <>
    <Helmet>
      <title>Search | FlixNet</title>
    </Helmet>
    
    {loading ? (
      <Loader />
    ) : (
      <Container> 
      <FormDiv>
        <Form onSubmit={handleSubmit}>
            <Input
              placeholder="Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†• Ìï¥Ï£ºÏÑ∏Ïöî"
              value={searchTerm}
              onChange={updateTerm}
              autoFocus
            />
            <SearchLogo onClick={handleSubmit}/>
        </Form>
      </FormDiv>
      {tvResults &&
          movieResults &&
          tvResults.length > 0 &&
          movieResults.length > 0 && (
            <Message size="2.5rem" text={`'${searched}'(Ïúº)Î°ú Í≤ÄÏÉâÌïú Í≤∞Í≥º`} color="white" />
          )}
        {movieResults && movieResults.length > 0 && (
          <Section count={movieResults.length} title="ÏòÅÌôî">
            {movieResults.map(movie => (
              <Poster
                key={movie.id}
                id={movie.id}
                imageUrl={movie.poster_path}
                title={movie.original_title}
                rating={movie.vote_average}
                year= { movie.release_date ? movie.release_date.substring(0, 4) : "none"}
                isMovie={true}
              />
            ))}
          </Section>
        )}
        {tvResults && tvResults.length > 0 && (
          <Section count={tvResults.length} title="ÎìúÎùºÎßà">
            {tvResults.map(show => (
              <Poster
                key={show.id}
                id={show.id}
                imageUrl={show.poster_path}
                title={show.original_name}
                rating={show.vote_average}
                year={ show.first_air_date ? show.first_air_date.substring(0, 4) : "none"}
                isMovie={false}
              />
            ))}
          </Section>
        )}
        {error && <Message size="1.5rem" color="#e74c3c" text={error} />}
        {tvResults &&
          movieResults &&
          tvResults.length === 0 &&
          movieResults.length === 0 && (
            <Message size="1.5rem" text="Í≤∞Í≥º ÏóÜÏùå" color="#95a5a6" />
          )}
          </Container>
    )}
    </>
);

SearchPresenter.propTypes = {
  movieResults: PropTypes.array,
  tvResults: PropTypes.array,
  error: PropTypes.string,
  searchTerm: PropTypes.string,
  searched: PropTypes.string,
  loading: PropTypes.bool.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  updateTerm: PropTypes.func.isRequired
};

export default SearchPresenter;
